.container {
  padding: 0 1.6rem;
  position: relative;
  display: flex;
  flex-direction: column;
  gap: 3rem;
  align-items: center;
  justify-content: center;
}
.container > :nth-child(1) {
  position: absolute;
  width: 8rem;
  left: 25%;
  top: -3rem;
}
.products {
  display: grid;
  width: 90%;
  grid-template-columns: 25% auto;
}
.menu {
  display: flex;
  flex-direction: column;
  list-style: none;
  gap: 2rem;
  font-weight: 500;
  font-size: 1.3rem;
}

.menu > li:hover {
  color: var(--pink);
  cursor: pointer;
}

.list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  height: 25rem;
  overflow: scroll;
  gap: 5rem;
  justify-content:space-between;
  }
.product1{
    width: 12rem;
    height: 8rem;
    background-color: white;
    position: relative;
    overflow: hidden;
    padding: 1rem;
    display: flex;
    margin-top: auto;
    border-radius: 2rem;
}
 .product1 > img { 
  top: 3rem;
  width: 6rem;
  height: 11rem;
} 
