.Testimonials{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center ;
    padding: 4rem 2rem;

}

.wrapper{
    display: grid;
    align-items: flex-end;
    width: 100%;
    grid-template-columns: 1fr 2fr 1fr;

}
.wrapper> img{
    /* width: 25rem;
    align-self: center; */
   display: block;
    margin-left: auto;
    margin-right: auto;
    width: 50%;
  
}

.container>:nth-child(1){
    font-size: 2rem;
    text-transform: uppercase;
    display: block;
}
.container>:nth-child(2){
    font-size: 0.8rem;
    text-transform: uppercase;
    display: block;
}
.wrapper>:nth-child(3){
text-align: right;
}

.reviews{
    font-size: 2rem;
    font-weight: bold;
    text-transform: uppercase;
}
.carousal{
    width: 100%;
}
.tCarousal{
    padding: 3rem;
}

.testimonial{
    display: flex;
    flex-direction: column;
    position: relative;
    background: white;
    border-radius: 0.5rem;
    align-items: center;
    justify-content: center;
    padding: 1rem;
    gap: 1rem;
    box-shadow: 0rem 1rem 3rem -50px #7d7d7d;
}
.testimonial > img{
    position: absolute;
    width: 3rem;
    top: -1.5rem;
    left: 45%;
}
.testimonial > :nth-child(2){
    padding-top: 8px;
}
.testimonial > :nth-child(3){
margin-bottom: -4px;
/* padding-bottom: px; */
}